<template>
  <div id="editBlog">
    <h2>编辑博客</h2>
    <form v-show="!submmited">
        <label>博客标题</label>
        <input type="text" v-model="blog.title" required>
        <label>博客内容</label>
        <textarea v-model="blog.content" ></textarea>
        <div id="checkboxes">
            <label>Vue.js</label>
            <input type="checkbox" value="Vue.js" v-model="blog.categories">
            <label>Node.js</label>
            <input type="checkbox" value="Node.js" v-model="blog.categories">
            <label>React.js</label>
            <input type="checkbox" value="React.js" v-model="blog.categories">
            <label>Angular.js</label>
            <input type="checkbox" value="Angular.js" v-model="blog.categories">
        </div>
        <label>作者：</label>
        <select v-model="blog.author">
            <option v-for="author in authors">
              {{author}}
            </option>
        </select>
        <button v-on:click.prevent="post">编辑博客</button>

    </form>
    <div  v-show="submmited">
        <h1>提交成功!</h1>
    </div>
    <div v-show="!submmited" id="preview">
        <h3>博客总览</h3>
        <p>博客标题:{{blog.title}}</p>
        <p>博客内容</p>
        <p>{{blog.content}}</p>
        <p>博客分类</p>
        <ul>
            <li v-for="category in blog.categories">
                {{category}}
            </li>
        </ul>
        <p>作者：{{blog.author}}</p>

    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'editBlog',
  data () {
    return {
     ids:this.$route.params.id,
     blog:{},
    authors:["Jim","Tom","Lily","Lucy"],
    submmited:false
       }
    },
  methods:{  
      editBlog(){
          axios.get('https://blog-43483.firebaseio.com/posts/'+this.ids+'.json').then(
              response=>{
                  this.blog=response.data;
              }
          )
      }, 
    post:function(){
      axios.put("https://blog-43483.firebaseio.com/posts/"+this.ids+'.json',this.blog).then(data=>{
        console.log(data.body);
        this.submmited=true
        })   }
    },
    created(){
       this.editBlog();
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  *{box-sizing: border-box}
  #editBlog{max-width:600px;margin:20px auto;padding:20px;}
  label{display:block;}
  textarea{height:200px;}
  input[type=text],textarea,select{display:block;width:100%;padding:10px;margin-top:10px}
  #checkboxes input{display:inline-block;margin-right:20px;}
  #checkboxes label{display:inline-block;}
button{margin-top:20px;padding:10px;background:crimson;color:#fff;border:none;font-size:18px;cursor:pointer;}
#preview{padding:10px 20px;border:1px solid #ccc;margin:30px 0;}
h3{margin-top:10px}
</style>



