<template>
  <div class="singleBlog">
    <h1>{{blog.title}}</h1>
    <article>{{blog.content}}</article>  
    <p>作者：{{blog.author}}</p> 
    <p>分类</p>
    <ul v-for="category in blog.categories">
      <li>{{category}}</li>  
    </ul> 
    <input @click="deleteSingleBlog" type="button" value="删除">
    <router-link :to="/edit/+ids"><input type="button" value="编辑"></router-link>
  </div>  
</template>
<script>
import axios from'axios'
export default {
  name:"singleBlog",
  data(){
      return {
          ids:this.$route.params.id,
          blog:{}
      }
  },
  created(){
      axios.get('https://blog-43483.firebaseio.com/posts/'+this.ids+'.json').then(
          data=>{
              this.blog=data.data
          }
      )
      
  } ,
  methods:{
    deleteSingleBlog(){
      axios.delete('https://blog-43483.firebaseio.com/posts/'+this.ids+'.json').then(
        data=>{
          this.$router.push({path:'/'})
        }
      )
    }
  } 
}
</script>
<style scoped>
.singleBlog{
    max-width:800px;
     margin:auto;
    padding:20px;
     background:#eee;
     border:1px dotted #aaa;
     color:#444;
}
</style>





