<template>
  <div v-theme:column="'narrow'" id="showBlogs">
    <h1>博客总览</h1>
    <input type="text" placeholder="搜索框" v-model="search">
    <div  v-for="blog in  filterBlogs" class="single-blog">
       <router-link v-bind:to="'/blog/'+blog.id"><h2 v-rainbow>{{blog.title | to-uppercase}}</h2></router-link>
       <article>
           {{blog.content | snippet}}
       </article>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {

  name: 'showBlogs',
 data(){
     return{
       blogs:[],
       search:''  
     }
 },
 created(){
     axios.get("https://blog-43483.firebaseio.com/posts.json")
     .then(
         data=>{
           return data.data;
         }).then(
          data=>{
            var blogArray=[];
            for(var key in data){
              data[key].id=key;
              blogArray.push(data[key]);
            }
            this.blogs=blogArray;
          }
         )    
 },
 computed:{
   filterBlogs:function(){
    return this.blogs.filter(blog=>{
       return blog.title.match(this.search)
        })
      }
    },
filters:{            //局部过滤器
  // toUppercase(value){
  //   return value.toUpperCase()
  // }
   "to-uppercase":function(value){
    return value.toUpperCase()
  }
}

}
</script>

<style scoped>
 #showBlogs{
     max-width:800px;
     margin:0 auto;
 }
 .single-blog{
     padding:20px;
     margin:20px 0;
     box-sizing:border-box;
     background:#eee;
     border:1px dotted #aaa;
     color:#444;
 }
  #showBlogs a{text-decoration: none;}
  input{padding:8px;width:100%;box-sizing: border-box;border:none}
</style>



